<template>
  <div class="comments-container"></div>
</template>

<script>
  export default {
    name: 'vue-intense-debate',

    props: {
      account: {
        type: String,
        required: true
      },

      pid: {
        type: String,
        required: true
      }
    },

    mounted () {
      window.idcomments_acct = this.account
      window.idcomments_post_id = this.pid
      window.idcomments_post_url = window.location.href

      var el = this.$el
      setTimeout(() => {
        var s = document.createElement('script')
        s.type = 'text/javascript'
        s.async = true
        s.src = 'https://www.intensedebate.com/js/genericCommentWrapperV2.js'
        el.appendChild(s)
      }, 0)
    }
  }
</script>



