<template>
  <div id="lv-container" data-id="city" :data-uid="uid"></div>
</template>

<script>
  export default {
    name: 'vue-livere',

    props: {
      uid: {
        type: String,
        required: true
      },

      refer: {
        type: String,
        required: true
      }
    },

    mounted () {
      window.refer = this.refer
      if (typeof window.LivereTower === 'function') {
        return
      }
      setTimeout(() => {
        var d = document
        var s = d.createElement('script')
        s.type = 'text/javascript'
        s.async = true
        s.src = 'https://cdn-city.livere.com/js/embed.dist.js'
        ;(d.head || d.body).appendChild(s)
      }, 0)
    }
  }
</script>



